.header {
    flex: 0 0 auto;
    /*justify-content: space-between;*/
    /*align-items: center;*/
    /*
	height: 56px;
	*/
    /*
    position: fixed;
    left: 0;
    top: 0;
	z-index: 50;
    width: 100%;
	*/
    padding: 0;
    margin: 0;
    background: #3066a9f0;
    /*background: #3066a9;*/
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    display: flex;
    flex-flow: row wrap;
}

.brand {
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    margin: 0;
    padding: 5px 15px;
}

@media screen and (max-width: 400px) {
    .brand h5 {
        visibility: hidden;
        display: none;
    }
}

.brand h1 {
    margin: 0;
    font-size: 24px;
    color: #fff;
}

.brand h5 {
    margin: 0;
    font-size: 14px;
    font-weight: 400;
    color: #fff;
}

.header nav {
    /*align-self: flex-end;*/
    flex-grow: 1;
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-end;
    font-size: 100%;
}

.logo {
    align-self: center;
    padding: 5px;
}

.header nav a img {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.header nav a {
    /*display: inline-block;*/
    height: 56px;
    line-height: 56px;
    padding: 0 15px;
    /*min-width: 50px;*/
    /*text-align: center;*/
    /*vertical-align: middle;*/
    background: rgba(255, 255, 255, 0);
    text-decoration: none;
    color: #fff;
    will-change: background-color;
}

.header nav a:hover,
.header nav a:active {
    background: rgba(0, 0, 0, 0.2);
}

.header nav a.active {
    background: rgba(0, 0, 0, 0.4);
}



